.droppable-hovered {
  outline: 5px solid green;
}
/* .droppable {
  border: 5px solid green;
} */

.droppable-overlay-visible {
  position: absolute;
  z-index: 1;

  background-color: rgba(0, 0, 0, 0.4);

  display: flex;
}

.droppable-overlay {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  justify-content: center;
}

.droppable-overlay-hidden {
  display: none;
}

.droppable-overlay svg {
  margin: auto;
  fill: white;
}
